main{
    margin: 0 auto;
    width: 100%;
    max-width: 1365px;
}

body{
    background: rgb(249,249,249);
}

.titulo{
    font-size: 30px;
    color: rgb(4,98,35);
    margin-top: 2%;
    text-align: center;
}

.linhatempo{
    margin-top: 2.5%;
    margin-left: 20%;
}

.divtexto{
    width: 700px;
    background-color: rgb(242,242,242);
    margin-top: 3%;
    text-align: left;
    padding: 1.5%;
    margin-left: 13%;
}

.divtexto h2{
    font-size: 18px;
    color: rgb(89,89,89)
}

.divtexto p{
    font-size: 16px;
    color: rgb(89,89,89)
}

.btn1{
    width: 100px;
    height: 30px;
    background-color: rgb(193, 196, 181);
    font-size: 12px;
    text-align: center;
    border-radius: 50px;
    cursor: pointer;
    padding: 5px;
    color: black;
    position:absolute;
    top: 88%;
    left: 45%;
}

.btn1:hover{
    opacity: 5s;
    color: white;
    transition: all 0.70s;
    background-color: rgb(188,213,65);
    text-decoration: none;
}

.btn2{
    width: 100px;
    height: 30px;
