    font-size: 12px;
    text-align: center;
    border-radius: 50px;
    cursor: pointer;
    padding: 5px;
    color: white;
    position:absolute;
    top: 88%;
    left: 53%;
}

.btn2:hover{
    opacity: 5s;
    color: black;
    transition: all 0.70s;
    background-color: rgb(193, 196, 181);
    text-decoration: none;
}

.boxpreco{
    width: 250px;
    height: 230px;
    background-color: rgb(62,19,78);
    margin-left: 67%;
    margin-top: -15.2%;
    border-radius: 5px;
    text-align: center;
    padding: 1%;
}

.boxpreco p{
    color: white;
}

.texto2:not(.igonreCss){
    color: rgb(188,213,65)
}

.boxpreco p{
    -webkit-margin-before: -0.6em;
}

.boxpreco h2{
    -webkit-margin-before: 0.5em;
