.btn1{
    width: 100px;
    height: 30px;
    background-color: rgb(193, 196, 181);
    font-size: 12px;
    text-align: center;
    border-radius: 50px;
    cursor: pointer;
    padding: 5px;
    color: black;
    position:absolute;
    top: 88%;
    left: 45%;
}

.btn1:hover{
    opacity: 5s;
    color: white;
    transition: all 0.70s;
    background-color: rgb(188,213,65);
    text-decoration: none;
}

