main{
    margin: 0 auto;
    width: 100%;
    max-width: 1365px;
}

body{
    background: rgb(249,249,249);
}

.titulo{
    font-size: 30px;
    color: rgb(4,98,35);
    margin-top: 2%;
    text-align: center;
}

.linhatempo{
    margin-top: 2.5%;
    margin-left: 20%;
}

.btn1{
    width: 100px;
    height: 30px;
    background-color: rgb(193, 196, 181);
    font-size: 12px;
    text-align: center;
    border-radius: 50px;
    cursor: pointer;
    padding: 5px;
    color: black;
    position:absolute;
    top: 88%;
    left: 45%;
}

.btn1:hover{
    opacity: 5s;
    color: white;
    transition: all 0.70s;
    background-color: rgb(188,213,65);
    text-decoration: none;
}

